<template>
  <div class="case">
    <mHeader goBack="true" ref="child1" :head-title="$t('individuation.case2Title')"></mHeader>
    <goTop/>
    <div class="caseTop">
      <caseTop :head-top="$t('individuation.case2Top')"></caseTop>
    </div>
    <div class="caseContent">
      <!--tab-->
      <van-tabs :active="active" class="caseVantTab">
        <van-tab>
          <div slot="title">
            <goodsTitle :goods-title="$t('other.goods')"></goodsTitle>
          </div>

          <div class="tabList">
            <div class="case2">
              <ModuleTwo2
                :twoP="$t('individuation.pi2mt2twoP')"
                :twoSpan="$t('individuation.pi2mt2twoSpan')"
                :twoSpan2="$t('individuation.pi2mttwoSpan2')"
                p21="true"
                p22="true"
              />
            </div>
            <div class="case9">
              <ModuleNine
                :nineP="$t('individuation.pi2mnnineP')"
                :nineEm="$t('individuation.pi2mnnineEm')"
              />
            </div>
            <div class="casew">
              <p>{{$t('individuation.pi2gj')}}</p>
            </div>
          </div>
        </van-tab>


        <van-tab>
          <div slot="title">
            <goodsTitle :goods-title="$t('other.details')"></goodsTitle>
          </div>
          <div class="tabList2">
            <div class="case10">
              <moduleTen2
                privateInner2="true"

                :tenDt1="$t('individuation.pi2tenDt1')"
                :tenDd1="$t('individuation.pi2tenDd1')"
                :tenDt2="$t('individuation.pi2tenDt2')"
                :tenDd2="$t('individuation.pi2tenDd2')"
                :tenDt3="$t('individuation.pi2tenDt3')"
                :tenDd3="$t('individuation.pi2tenDd3')"
                :tenDt4="$t('individuation.pi2tenDt4')"
                :tenDd4="$t('individuation.pi2tenDd4')"
                :tenDt5="$t('individuation.pi2tenDt5')"
                :tenDd5="$t('individuation.pi2tenDd5')"
                :tenDt6="$t('individuation.pi2tenDt6')"
                :tenDd6="$t('individuation.pi2tenDd6')"
                :tenDt7="$t('individuation.pi2tenDt7')"
                :tenDd7="$t('individuation.pi2tenDd7')"
              />
            </div>
          </div>
        </van-tab>

      </van-tabs>
    </div>
    <mFooter/>
  </div>
</template>

<script>
  import mHeader from '../../../components/header/headerTab.vue'
  import caseTop from '../../../components/caseTopScreen.vue'
  import goodsTitle from '../../../components/goods.vue'
  import mFooter from '../../../components/footer/footer.vue'

  import ModuleTwo2 from "../../../components/module/moduleTwo2.vue"
  import ModuleNine from "../../../components/module/moduleNine.vue"
  import ModuleTen2 from "../../../components/module/ModuleTen2.vue"
  import goTop from '../../../components/goTop.vue';

  export default {
    name: 'page-tab-container',
    data() {
      return {
        active: 0,
        listProcess:[{'dd':'JSS特供红色系花卉，打造西班牙式的热情。','dt':'花卉'},{'dd':'专业化妆师定制生日造型，做最美的寿星。','dt':'妆容'},{'dd':'JSS特供粉红色主题花卉，少女感十足。','dt':'花卉'},{'dd':'专业摄影师跟拍，捕捉精彩瞬间。另有摄像师全程记录并剪辑，根据需求制作不同风格VCR。','dt':'记录'},{'dd':'VR游戏、世界百大DJ主持派对、惊喜生日短片。','dt':'娱乐'},{'dd':'邀请函设计、晚宴菜单设计及制作。','dt':'物料设计'}]
      };
    },
    mounted () {
      window.addEventListener('scroll', this.handleScroll)
      this.handleScroll();
    },
    destroyed () {
      // 离开页面移除
      window.removeEventListener('scroll', this.handleScroll)
    },
    methods:{
      handleClick(tab, event) {
        console.log(tab, event);
      },
      handleScroll() {
        let scrollTop = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop;
        //console.log(scrollTop);
        let opcaity = (scrollTop / 100 > 1) ? 1 : scrollTop / 100;
        this.$refs.child1.headerBac(opcaity);
      }
    },
    components: {
      mHeader,
      mFooter,
      caseTop,
      goodsTitle,
      ModuleTwo2,
      ModuleNine,
      ModuleTen2,
      goTop
    }
  }
</script>
<style lang="scss" scoped>
  @import "../../../style/mixin";
  .case{
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 102;
    background: #070705;
    .caseTop{
      padding-top: .88rem;
      width: 100%;
    }
    .caseContent{
      .caseVantTab{
        height: 100%;
        .tabList{
          background: #fff;
          padding-top: 2rem;
          .case2{
          }
          .case9{
            margin-top: .8rem;
          }
          .casew{
            padding: .3rem .6rem .8rem .6rem;
            p{
              line-height: .36rem;
              @include sc(.26rem,#000);
            }
          }
        }
        .tabList2{
          background: #fff;
          .case10{
            padding-bottom: .8rem;
            padding-top: 1.82rem;
          }
        }
      }
    }
  }

</style>



