</template>

<script>
  export default {
    props: ['nineP','nineEm','nineSpan'],
    name: "",
    data() {
      return {

      }
    },
    component: {}
  }
</script>

<style lang="scss" scoped>
  @import "../../style/mixin";
  .tabListThree{
    p{
      padding-left: 1.1rem;
      padding-bottom: .6rem;
      padding-right: .6rem;
      @include sc(.26rem,#000);
      line-height:.36rem;
      display: flex;
      justify-content: space-between;
    }
    .tabThreeOimg{
      display: flex;
      justify-content: flex-end;
      img{
        width: 3.68rem;
        height: 3.06rem;
        padding-right: .46rem;
      }
    }
    .tabThreeTimg{
      padding-left: 1.06rem;
      padding-right: .46rem;
      @include fjb;
      img{
        width: 2.66rem;
        height: 4rem;
      }
      em{
        @include sc(.3rem,#000);
        font-style: normal;
      }
    }

    span{
      display: block;
      text-align: center;
      padding-top: .2rem;
      @include sc(.3rem,#000);
    }
  }
</style>



