<template>
    <div class="moduleTwo">
      <p>{{twoP}}</p>
      <div class="mto">
        <dl>
          <dt>{{twoSpan}}</dt>
        </dl>
        <div v-if="p21" class="mtoimg">
          <img src="../../images/case/private/p21.jpg" alt="">
        </div>
      </div>
      <div class="mto2">
       <!-- <span>{{twoSpan2}}</span>-->
        <div v-if="p22" class="mto2img">
          <img src="../../images/case/private/p22.jpg" alt="">
        </div>
        <dl>
          <!--<dd>规模：2人</dd>
          <dd>定位：求婚</dd>-->
          <dd>{{twoSpan2}}</dd>
        </dl>
      </div>
    </div>
</template>

<script>
  export default {
    props: ['twoP','twoSpan','twoSpan2','p21','p22'],
    name: "",
    data() {
      return {

      }
    },
    component: {}
  }
</script>

<style lang="scss" scoped>
  @import "../../style/mixin";
  .moduleTwo{
    padding-left: .45rem;
    padding-right: .2rem;
    p{
      @include sc(.26rem,#000);
      text-align: justify;
      text-indent: .45rem;
      line-height: .36rem;
      padding-right: .5rem;
    }
    .mto{
      display: flex;
      justify-content: flex-end;
      padding-top: .8rem;
      dl{
        margin-right: .3rem;
        width: 3rem;
        @include sc(.26rem,#000);
        @include fjc;
        dt{
          font-size: .28rem;
          margin-bottom: .12rem;
        }
      }
      .mtoimg{
        width: 3.26rem;
        height: 2.16rem;
        background: red;
        img{
          width: 100%;
          height: 100%;
        }
      }
    }
    .mto2{
      @include fjsb;
      margin-top: .8rem;
      dl{
        width: 3rem;
        dd{
          @include sc(.26rem,#000);
          line-height: .5rem;
          text-align: right;
        }
      }
      .mto2img{
        @include wh(3.58rem,3rem);
        background: red;
        img{
          width: 100%;
          height: 100%;
        }
      }
    }

  }
</style>



